.bx-controls-direction {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    left: 0;
    z-index: 11;
}

.bx-prev, .bx-next {
    position: relative;
    width: 78px;
    height: 79px;
}

.bx-prev {
    background: url('../img/arrow-left-b.png') no-repeat 50% 50% / cover;
}
.bx-next {
    background: url('../img/arrow-right-b.png') no-repeat 50% 50% / cover;
}