.nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;

    &-link {
    	outline: none;
    	&:hover,&:focus {
    		color: $dark;
    		outline: none;
    	}
    }

    &-pills {
		
		.nav-link {
			font-size: 19px;
			color: $dark;
			font-weight: 300;
			margin-bottom: 15px;
			position: relative;

			&:before {
				content: '';
				display: block;
				width: 15px;
				height: 15px;
				border-radius: 50%;
				background: $white;
				position: absolute;
				left: -15px;
				top: 12.5px;
				border: 2px solid $dark;
			}

			&.active {
				background: transparent;
				color: $dark;
				font-weight: 600;

				&:before {
					background: $dark;
				}
			}
		}
    }

}